* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 36px;
}

p {
  font-family: "Roboto", sans-serif;
}

label {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
  color: #aaaaaa;
}

input[type="text"] {
  height: 30px;
  outline: none;
  width: 250px;
  border: none;
  border-bottom: solid 2px;
  max-width: 100%;
  display: block;
  padding: 20px 10px;
  line-height: 1.42857143;
  font-size: 14px;
  margin-top: 7.5px;
}

input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

button {
  font-size: 14px;
  height: 40px;
  width: 175px;
  outline: none;
  cursor: pointer;
  border: solid 2px;
  background-color: white;
}

button:hover {
  background-color: black;
  color: white;
  transition: 0.4s;
  border: solid 2px black;
}

@font-face {
  font-family: 'StarWarsFont';
  src: url('./assets/fonts/Starjedi.woff') format('woff')
}

.transition-group {
  flex: 1;
  position: relative;
}

.router {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.page {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.fade-enter .page {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active .page {
  opacity: 1;
  transition: opacity 450ms;
}
